import Vue from 'vue'
import VueI18n from 'vue-i18n'
import axios from 'axios'

Vue.use(VueI18n)

Vue.prototype.$t = function (key, locale, values) {
    const result =  this.$i18n.t(key, locale, values);
    if (result === key) {
        return '';
    }
    return result;
};

export const i18n = new VueI18n({
    locale: 'ru',
    fallbackLocale: 'ru'
})

export async function loadLanguageAsync () {
    const res = await axios.get('https://xn--d1abkftnrcqv.xn--p1ai/content/texts', {
        withCredentials: false
    })
    i18n.setLocaleMessage('ru', res.data)
    i18n.locale = 'ru'
}
